import React from 'react';
import {
Nav,
NavLink,

NavMenu,

} from './NavbarElements';

const Navbar = () => {
return (
	
   
	<Nav>
		
  
		<NavMenu>
		<NavLink to="/" activeStyle>
                    HOME
                </NavLink>
		<NavLink to='/veg' activeStyle>
			VEG FOOD ITEMS
		</NavLink>
		<NavLink to='/nonveg' activeStyle>
			NON-VEG FOOD ITEMS
		</NavLink>
		<NavLink to='/starters' activeStyle>
			STARTERS
		</NavLink>
		<NavLink to='/sweets' activeStyle>
			SWEETS
		</NavLink>
		<NavLink to='/blogs' activeStyle>
			SPECIAL DISHES
		</NavLink>
	
	
		</NavMenu>
		
	</Nav>

);
};

export default Navbar;
